import {observable} from "mobx";

export interface ITileDefinition {
  letter: string;
  count: number;
  points: number;
}

export class TileDefinition implements ITileDefinition {
  @observable
  public letter: string;

  @observable
  public count: number;

  @observable
  public points: number;

  constructor(props: ITileDefinition) {
    this.letter = props.letter;
    this.count = props.count;
    this.points = props.points;
  }
}

export interface ITileSet {
  name: string;
  tiles: ITileDefinition[];
}

export class TileSet implements ITileSet {
  @observable
  public name: string;
  
  @observable
  public tiles: ITileDefinition[];

  constructor(name?: string, tiles?: ITileDefinition[]) {
    this.name = name || "Untitled";
    this.tiles = tiles || [];
  }
}

