import { ITile } from './App';
import { IPlay } from "./Move";

type GoalType = 'Get Moving' | 'Over It' | 'Double Up' | 'Up Front' | 'Cheapskate' | 'Shortie';

export interface IGoal {
    type: GoalType;
    description: string;
    points: number;
    possible(board: ITile[]): boolean | "maybe";
    check(move: IPlay): boolean;
}

abstract class Goal implements IGoal {
    abstract type: GoalType;
    abstract get description(): string;
    abstract points: number;
    possible(board: ITile[]) {
        const letters = board.map((t) => t.letter).join("");
        return this.possibleWithBoard(letters);
    }
    check(move: IPlay): boolean {
        const word = move.word.map((t) => t.letter).join("");
        return this.checkWord(word);
    }
    abstract possibleWithBoard(board: string): boolean;
    abstract checkWord(word: string): boolean;
}

export class VerbGoal extends Goal {
    type = "Get Moving" as GoalType;
    description = "Play something ending in 'ing'";
    points = 10;
    possibleWithBoard(board: string) {
        return board.includes("I") &&board.includes("N") &&board.includes("G");        
    }
    checkWord(word: string): boolean {
        return word.endsWith("ING");
    }
    public static fromJson(json: any) {
        return new VerbGoal();
    }
}

export class VerbPastGoal extends Goal {
    type = "Over It" as GoalType;
    description = "Play something ending in 'ed'";
    points = 8;
    possibleWithBoard(board: string) {
        return board.includes("E") &&board.includes("D");        
    }
    checkWord(word: string): boolean {
        return word.endsWith("ED");
    }
    public static fromJson(json: any) {
        return new VerbPastGoal();
    }
}

export class DoublesGoal extends Goal {
    type = "Double Up" as GoalType;
    get description() { return `Play something with two '${this.letter}'s`; }
    points = 7;
    private readonly letter: string;
    constructor(letter: string) {
        super();
        this.letter = letter;
    }
    possibleWithBoard(board: string) {
        return board.split(this.letter).length >= 3;
    }
    checkWord(word: string): boolean {
        return word.split(this.letter).length >= 3;
    }
    public static fromJson(json: any) {
        return new DoublesGoal(json.letter);
    }
}

export class StartsWithGoal extends Goal {
    type = "Up Front" as GoalType;
    get description() { return `Play something that starts with '${this.letter}'`; }
    points = 5;
    private readonly letter: string;
    constructor(letter: string) {
        super();
        this.letter = letter;
    }
    possibleWithBoard(board: string) {
        return board.split(this.letter).length >= 2;
    }
    checkWord(word: string): boolean {
        return word.startsWith(this.letter);
    }
    public static fromJson(json: any) {
        return new StartsWithGoal(json.letter);
    }
}

export class CheapskateGoal implements IGoal {
    possible(board: ITile[]) {
        if (board.filter((e) => (e.points || 0) < 2).length >= 3) {
            return "maybe";
        }
        return false;
    }
    check(move: IPlay): boolean {
        return !move.word.some((t) => (t.points || 0) > 2);
    }
    type = "Cheapskate" as GoalType;
    get description() { return `Only use tiles worth one or two points`; }
    points = 10;
    public static fromJson(json: any) {
        return new CheapskateGoal();
    }
}

export class LengthGoal implements IGoal {
    length: number;
    constructor(length: number) {
        this.length = length;
    }
    possible(board: ITile[]) {
        return true;
    }
    check(move: IPlay): boolean {
        return move.word.length === this.length;
    }
    type = "Shortie" as GoalType;
    get description() { return `Play something ${this.length} letters long`; }
    points = 10;
    public static fromJson(json: any) {
        return new LengthGoal(json.length);
    }
}
