import { ITile } from 'App';
import { getPrefixInfo } from './isWord';

export default class WordFinder {
    private board: ITile[];
    private width: number;
    private onWordFound: (word: ITile[]) => void;

    constructor(board: ITile[], width: number, onWordFound: (word: ITile[]) => void) {
        this.board = board;
        this.width = width;
        this.onWordFound = onWordFound;
    }

    public findWords() {
        for (let y = 0; y < this.board.length / this.width; y++) {
            for (let x = 0; x < this.width; x++) {
                const seed = this.width * y + x;
                this.search(seed, []);
            }            
        }
    }

    private adjacent(pos: number) {
        const {board, width} = this;
        const x = pos % width;
        const y = (pos - x) / width;
        const result: number[] = [];
        if (x > 0) {
            result.push(pos - 1);
            if (y > 0) {
                result.push(pos - 1 - width);
            }
            if (y < (board.length / width) - 1) {
                result.push(pos - 1 + width);
            }
        }
        if (y > 0) {
            result.push(pos - width);
        }
        if (x < width - 1) {
            result.push(pos + 1);
            if (y > 0) {
                result.push(pos + 1 - width);
            }
            if (y < (board.length / width) - 1) {
                result.push(pos + 1 + width);
            }
        }
        if (y < (board.length / width) - 1) {
            result.push(pos + width);
        }
        return result;
    }

    private adjacentFree(pos: number, word: ITile[]) {
        return this.adjacent(pos).filter((e) => {
            const tile = this.board[e];
            if (tile.type !== "letter") {
                return false;
            }
            if (word.includes(tile)) {
                return false;
            }
            return true;
        });
    }

    private search(pos: number, word: ITile[]) {
        const tile = this.board[pos];
        if (tile.type !== "letter") {
            return;
        }
        word.push(tile);
        const info = getPrefixInfo(word);
        if (info === undefined) {
            return;
        }
        if (info === true || info.valid) {
            this.onWordFound(word);
        }
        if (info !== true) {
            const af = this.adjacentFree(pos, word);
            af.forEach((nextPos) => {
                const nextTile = this.board[nextPos];
                if (info.next.includes(nextTile.letter.toLowerCase())) {
                    this.search(nextPos, [...word]);
                }
            });
        }
    }
}
