export type TrieEntry = Trie | 1 | undefined;
export type Word = Array<{ letter: string }>;

interface Trie {
  [key: string]: TrieEntry,
}

const TRIE: Trie = require("./trie.json");

function find(word: Word) {
  let t: TrieEntry = TRIE;
  word.forEach((tile) => {
    if (typeof t === "object") {
      t = t[tile.letter.toLocaleLowerCase()];
    }
  });
  return t;
}

export default function isWord(word: Word) {
  const t = find(word);
  return typeof t === "object" && t["_"] === 1;
}

export function getPrefixInfo(prefix: Word) {
  const t = find(prefix);
  if (typeof t !== "object") {
    return undefined;
  }
  const next = Object.getOwnPropertyNames(t);
  const validIndex = next.indexOf("_");
  const valid = validIndex >= 0;
  if (valid) {
    next.splice(validIndex, 1);
  }
  if (next.length === 0) {
    return true;
  }
  return { valid, next }
}
