import React from "react";
import { observer } from "mobx-react";
import { ITileSet, ITileDefinition } from "TileDefinitions";
import Card from "react-bootstrap/Card";
import { ITile } from "App";

interface ITileSetComponentProps {
    tileSet: ITileSet;
    bag?: ITile[];
}

interface ITileSetComponentState {

}

@observer
class TileDefinitionComponent extends React.Component<{ tileDefinition: ITileDefinition, remaining?: number }> {
    public render() {
        const { remaining, tileDefinition } = this.props;
        const { letter, points, count } = tileDefinition;
        return <div className="tile-definition">
            <div className="tile">
                <div className="field">
                    <div className="letter">{letter}</div>
                    <div className="points">{points}</div>
                </div>
            </div>
            &nbsp;x&nbsp;{remaining ? `${remaining}/` : ""}{count}
        </div>
    }
}

@observer
export class TileSetComponent extends React.Component<ITileSetComponentProps, ITileSetComponentState> {
    public render() {
        const { name, tiles } = this.props.tileSet;
        return <Card className="moves">
            <Card.Header>{name}</Card.Header>
            <Card.Body>
                {tiles.map((tileDefinition, index) => {
                    let remaining: number | undefined = undefined;
                    if (this.props.bag) {
                        remaining = this.props.bag.filter((tile) => tile.letter === tileDefinition.letter).length;
                    }
                    return <TileDefinitionComponent
                        key={index}
                        tileDefinition={tileDefinition}
                        remaining={remaining} />;
                })}
            </Card.Body>
        </Card>
    }
}

export default TileSetComponent;
