import React from "react";
import Accordion from "react-bootstrap/Accordion";
import Form from 'react-bootstrap/Form';
import TileSetComponent from 'TileSetComponent';
import { IScoochOptions, ITile } from "App";
import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import AccordionCell from "AccordionCell";
import { TileSet } from "TileDefinitions";

interface IRulesProps {
    options: IScoochOptions;
    tileSet: TileSet;
    bag: ITile[];

    changeOptions(change: (options: IScoochOptions) => IScoochOptions): void;
    resetOptions(): void;
}

interface IRulesState {
  version: string;
  activeEventKey: string | undefined;
}

const SESSION_STORAGE_KEY = "rulesState";

const CURRENT_VERSION = "0.4.2";

export default class RulesComponent extends React.Component<IRulesProps, IRulesState> {    
    constructor(props: IRulesProps) {
        super(props);
        const stateString = sessionStorage.getItem(SESSION_STORAGE_KEY);
        if (stateString) {
            try {
                const state = JSON.parse(stateString) as IRulesState;
                if (state.version !== CURRENT_VERSION) {
                  state.activeEventKey = "help-new";
                  state.version = CURRENT_VERSION;
                }
                this.state = state;
                return;
            } catch {
                sessionStorage.removeItem(SESSION_STORAGE_KEY);
            }
        }
        this.state = { activeEventKey: "help-new", version: CURRENT_VERSION };
    }

    public componentDidUpdate() {
        sessionStorage.setItem(SESSION_STORAGE_KEY, JSON.stringify(this.state));
    }

    public render() {
        const {options, tileSet, bag, changeOptions, resetOptions} = this.props;
        return <Accordion className="mt-5" defaultActiveKey={this.state.activeEventKey}>
        <AccordionCell onSelect={this.onSelect} eventKey="help-new" title={`What’s New (v${CURRENT_VERSION})`}>
          <dl>
            <dt>Goals</dt>
            <dd>There are now randomly-selected bonus goals shown below the play area. Complete them to earn bonus points!</dd>

            <dt>Automation</dt>
            <dd>Play against the computer, or have it take over for someone! Select the "Automated" option under a player’s name.</dd>
          </dl>
        </AccordionCell>

        <AccordionCell onSelect={this.onSelect} eventKey="help-words" title="Making Words">
          <ul>
            <li>Click <span className="field">tiles</span> to add them to your word.</li>
            <li>Click a <span className="field selected">selected</span> tile to remove it from your word.</li>
            <li>You may only add tiles <span className="field">close</span> to the last letter of your word — other tiles are <span className="field unselectable">too far</span>.</li>
            <li>Never fear! You may scooch tiles around to make the word you want! Whether <span className="field">near</span> or <span className="field unselectable">far</span>, just drag tiles to where you need them.</li>
            <li>When you scooch a tile, it switches places with its neighbor, and both tiles lose their point values.</li>
            {options.oneScoochPerTurn && <li><Accordion.Toggle as={Badge} eventKey="options" variant="secondary">Option</Accordion.Toggle> You may only scooch one tile per turn, though you can move it as far as you want, and don't have to do it all at once.</li>}
            {options.bonusForLongerWords && <li><Accordion.Toggle as={Badge} eventKey="options" variant="secondary">Option</Accordion.Toggle> Play longer words to get bonus points!</li>}
          </ul>
        </AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="help-end-turn" title="Ending Your Turn"><p>
          Once you’re happy with your word, click the <Badge variant="primary">Play Word</Badge> button.
          e tiles that make up your word go onto the scoreboard,
            tiles left on the board drop down into their places,
              w tiles slide in from the top to fill up the board.
                s you scooched but didn’t use get their points back,
          and it’s the next player’s turn!
        </p></AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="help-nuke" title="The Nuclear Option"><p>
          Also known as “flipping the table,” once per game each player may use the <Badge variant="warning">Nuke</Badge> button to throw all the tiles on the board back into the bag and draw new ones.
          There is no penalty — you won’t lose your turn or any points — but you may only use the nuclear option once!
        </p></AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="help-end" title="End of the Game"><p>
          When the tile bag is empty, you’ll notice empty spaces left on the board after tiles drop down.
          (You can't scooch with those empty spaces.)
          Eventually, there won’t be more words to make, and you’ll have to <Badge variant="secondary">Pass</Badge>.
          After all players pass in a row, the game ends, and the player with the most points wins!
        </p></AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="help-tiles" title="Tile Distribution">
          <TileSetComponent tileSet={tileSet} bag={bag} />
        </AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="help-proto" title="Prototype Notes"><dl>
          <dt>Scooch Limit</dt>
          <dd>
            When playing with the physical prototype, we generally made the rule that you could only scooch one tile
            per turn, though you could move it as far as you wanted.Right now this version does <i>not</i> limit you
            to a single scooch per turn, but that may change.
          </dd>
          <dt>Point Values</dt>
          <dd>
            The big tiles (
            <span className="field">J</span>
            <span className="field">Q</span>
            <span className="field">X</span>
            <span className="field">Z</span>) are way too valuable — without the riposte available in games like
            rabble™ and Words With Friends™ they’re completely dominant in final scoring.I did run a test
                 physical prototype where scores were compressed, with the highest score being five, and the
                  re far more balanced (and felt fair) but were also boring.I need to find a blance — perhaps
            with the big tiles scoring seven points — to restore the excitement and variability.
          </dd>
        </dl></AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="options" title="Options">
          <Form.Check type="checkbox" id="on-scooch" label="One scooch per turn" checked={!!options.oneScoochPerTurn} onChange={() => changeOptions((originalOptions) => {
            const options = {...originalOptions};
            options.oneScoochPerTurn = !options.oneScoochPerTurn;
            return options;
          })} />
          <Form.Check type="radio" id="on-length-none" label="No bonus points for longer words" checked={!!options.bonusForLongerWords === false} onChange={() => changeOptions((originalOptions) => {
            const options = {...originalOptions};
            options.bonusForLongerWords = false;
            return options;
          })} />
          <Form.Check type="radio" id="on-length" label="One bonus point for each letter over four tiles" checked={options.bonusForLongerWords === "constant"} onChange={() => changeOptions((originalOptions) => {
            const options = {...originalOptions};
            options.bonusForLongerWords = "constant";
            return options;
          })} />
          <Form.Check type="radio" id="on-length-linear" label="Increasing bonus points for words over three tiles" checked={options.bonusForLongerWords === "linear"} onChange={() => changeOptions((originalOptions) => {
            const options = {...originalOptions};
            options.bonusForLongerWords = "linear";
            return options;
          })} />
          <Button onClick={() => resetOptions()}>Reset</Button>
        </AccordionCell>
    
        <AccordionCell onSelect={this.onSelect} eventKey="dictionary" title="Acceptable Words">
          <p>
            Words need to be at least three letters long and are checked against the <a href="https://diginoodles.com/The_English_Open_Word_List_%28EOWL%29" target="_blank" rel="noopener noreferrer">English Open Word List</a> (EOWL).
          </p>
          <dl>
            <dt>
              UK Advanced Cryptics Dictionary Licensing Information
            </dt>
            <dd>
              Copyright © J Ross Beresford 1993-1999. All Rights Reserved.The following restriction is placed on the use of this publication: if the UK Advanced Cryptics Dictionary is used in a software package or redistributed in any form, the copyright notice must be prominently displayed and the text of this document must be included verbatim.
            </dd>
            <dt>
              <a href="https://diginoodles.com/The_English_Open_Word_List_%28EOWL%29" target="_blank" rel="noopener noreferrer">The English Open Word List</a>
            </dt>
            <dd>
              The EOWL was developed by Ken Loge, and based on the above UKACD, but he explicitly placed no additional rescrtictions on its use.
            </dd>
          </dl>
    
        </AccordionCell>
      </Accordion>;
    }

    private onSelect = (activeEventKey: string) => this.setState((state) => {
        if (state.activeEventKey === activeEventKey) {
            return {activeEventKey: undefined};
        } else {
            return {activeEventKey};
        }
    });
}
