import React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';

export default function AccordionCell(props: {
  eventKey: string;
  title: React.ReactNode;
  children?: React.ReactNode;
  onSelect(eventKey: string): void;
}) {
    const onClick = props.onSelect ? () => props.onSelect(props.eventKey) : undefined;
  return <Card>
    <Accordion.Toggle as={Card.Header} eventKey={props.eventKey} style={{ cursor: "pointer" }} onClick={onClick}>
      {props.title}
    </Accordion.Toggle>
    <Accordion.Collapse eventKey={props.eventKey}>
      <Card.Body>
        {props.children}
      </Card.Body>
    </Accordion.Collapse>
  </Card>;
}
